import React from "react";
import Btn from "@ezycore/ui/components/atoms/Btn";
import Avatar from "@ezycore/ui/components/molecules/Avatar";

const ListCard = ({ family }) => {
  if (!family) return;

  return (
    <>
      <div className="list-card">
        <div className="list-card__inner">
          <div className="list-card__title d-flex gap-2 items-center">
            <Avatar />
            {family && family.familyName && (
              <span className="h4">{family.familyName}</span>
            )}
          </div>
          <div className="list-card__info">
            <div className="info-block">
              {family &&
                family.parents &&
                family.parents.map((parent, key) => (
                  <div className="info-item" key={key}>
                    <span>{parent.firstname}</span>
                    <span>{parent.lastname}</span>
                    <span>({parent.rol})</span>
                  </div>
                ))}
            </div>
            <div className="info-block">
              {family &&
                family.students &&
                family.students.map((student, key) => (
                  <div className="info-item" key={key}>
                    <span>{student.name}</span>
                    <span>(x ans | Yourte)</span>
                  </div>
                ))}
            </div>
          </div>
          <div className="list-card__actions">
            <Btn to={`/family/${family.familyId}`}>Voir la fiche</Btn>
          </div>
        </div>
      </div>
    </>
  );
};

export default ListCard;
